// AuthContext.js
import React, { createContext, useContext, useState, useEffect } from 'react';
import api from '../services/api'; // Import your API module

const AuthContext = createContext();

export const AuthProvider = ({ children }) => {
  const [authState, setAuthState] = useState({
    isAuthenticated: !!localStorage.getItem('token'),
    user: null,
    loading: true // Add loading state to handle initial loading state
  });

  const fetchUser = async () => {
    try {
      console.log('Fetching user...');
      const response = await api.get('/user/profile', {
        headers: {
          'x-auth-token': localStorage.getItem('token'),
        },
      });

      console.log('User response:', response.data);

      // Return the user data
      return response.data;
    } catch (error) {
      console.error('Error fetching user:', error);
      return null;
    }
  };

  const login = async () => {
    // Perform login logic, and set isAuthenticated to true
    setAuthState(prevState => ({ ...prevState, loading: true }));
    try {
      console.log('Token before fetching user:', localStorage.getItem('token'));

      // Fetch and set user information
      const userData = await fetchUser();
      setAuthState({
        isAuthenticated: true,
        user: userData,
        loading: false
      });
      console.log(userData);
    } catch (error) {
      console.error('Error logging in:', error);
      setAuthState(prevState => ({ ...prevState, loading: false }));
    }
  };

  const logout = () => {
    // Perform logout logic, and set isAuthenticated to false
    localStorage.removeItem('token');
    setAuthState({
      isAuthenticated: false,
      user: null,
      loading: false
    });
  };

  useEffect(() => {
    // On component mount, check if the user is authenticated and fetch user data
    if (authState.isAuthenticated) {
      fetchUser()
        .then(userData => {
          setAuthState(prevState => ({
            ...prevState,
            user: userData,
            loading: false
          }));
        })
        .catch(error => {
          console.error('Error fetching user:', error);
          setAuthState(prevState => ({
            ...prevState,
            isAuthenticated: false,
            loading: false
          }));
        });
    }
  }, [authState.isAuthenticated]);

  return (
    <AuthContext.Provider value={{ ...authState, login, logout }}>
      {children}
    </AuthContext.Provider>
  );
};

export const useAuth = () => {
  return useContext(AuthContext);
};




