// App.js
import React from 'react';
import { BrowserRouter as Router, Routes, Route, Navigate } from 'react-router-dom';
import Home from './pages/Home';
import Dashboard from './pages/Dashboard';
import Login from './pages/Login';
import Register from './pages/Register';
import Layout from './components/Layout';
import { AuthProvider, useAuth } from './context/AuthContext';
import AddAdmin from './pages/AddAdmin';
import Vouchers from './pages/Vouchers';
import AddCustomerForm from './pages/AddCustomerForm';
import LoginCustomerForm from './pages/LoginCustomerForm';

const App = () => {
  return (
    <AuthProvider>
      <Router>
        <Routes>
          <Route path="/" element={<Layout><Home /></Layout>} />
          <Route path="/dashboard" element={<ProtectedRoute component={<Layout><Dashboard /></Layout>} />} />
          <Route path="/addadmin" element={<ProtectedRoute component={<Layout><AddAdmin /></Layout>} />} />
          <Route path="/vouchers" element={<ProtectedRoute component={<Layout><Vouchers /></Layout>} />} />
          <Route path="/addcustomer" element={<ProtectedRoute component={<Layout><AddCustomerForm /></Layout>} />} />
          <Route path="/login" element={<Layout><Login /></Layout>} />
          <Route path="/register" element={<Layout><Register /></Layout>} />
          <Route path="/customerlogin" element={<Layout><LoginCustomerForm /></Layout>} />
        </Routes>
      </Router>
    </AuthProvider>
  );
};

// A helper component for handling protected routes
const ProtectedRoute = ({ component }) => {
  const { isAuthenticated } = useAuth();

  if (isAuthenticated) {
    return component;
  } else {
    // Redirect to the login page if not authenticated
    return <Navigate to="/login" />;
  }
};

export default App;
